import { writable } from "svelte/store";
import { Suits, Ranks } from "@/modules/Cards";
const deck = writable([]);
export function resetDeck() {
    // Create new array
    const result = [];
    // Iterate over suits and ranks, adding to array
    for (const suit of Suits) {
        for (const rank of Ranks) {
            result.push({ suit, rank, state: 0 });
        }
    }
    // Set deck store to array
    deck.set(result);
}
export function shuffleDeck() {
    deck.update(array => {
        let currentIndex = array.length;
        let temporaryValue;
        let randomIndex;
        while (currentIndex != 0) {
            randomIndex = Math.floor(Math.random() * currentIndex);
            currentIndex -= 1;
            temporaryValue = array[currentIndex];
            array[currentIndex] = array[randomIndex];
            array[randomIndex] = temporaryValue;
        }
        return array;
    });
}
export function drawFromDeck(count) {
    let drawn = [];
    deck.update(cards => {
        if (count > 0 && count <= cards.length) {
            drawn = cards.splice(0, count);
        }
        return cards;
    });
    return drawn;
}
