import { Rank } from "@/modules/Cards/Rank";
import { Suit } from "@/modules/Cards/Suit";
export function getCardIndex(x, y) {
    if (typeof x == "number" && y != null) {
        // Assume func was given rank and suit
        return y * 100 + x;
    }
    else if (typeof x == "object") {
        // Assume x is a card object
        return x.suit * 100 + x.rank;
    }
    return null;
}
export function getCardName(x, y) {
    let rank, suit;
    if (typeof x == "number" && y != null) {
        // Assume func was given rank and suit
        suit = x;
        rank = y;
    }
    else if (typeof x == "object") {
        // Assume x is a card object
        suit = x.suit;
        rank = x.rank;
    }
    // Return result
    if (suit == Suit.Joker)
        return Suit[suit];
    else
        return `${Rank[rank]} of ${Suit[suit]}`;
}
// Compares two cards
export function compareCards(first, second) {
    return first.suit != second.suit
        ? first.suit > second.suit
            ? 1
            : -1
        : first.rank == second.rank
            ? 0
            : first.rank > second.rank
                ? 1
                : -1;
}
