export var Suit;
(function (Suit) {
    Suit[Suit["Clubs"] = 1] = "Clubs";
    Suit[Suit["Diamonds"] = 2] = "Diamonds";
    Suit[Suit["Hearts"] = 3] = "Hearts";
    Suit[Suit["Spades"] = 4] = "Spades";
    Suit[Suit["Joker"] = 5] = "Joker";
})(Suit || (Suit = {}));
export const Suits = Object.keys(Suit).filter(x => Number.isNaN(Number(x))).map(x => Suit[x]).filter(x => x != Suit.Joker);
