import { writable, derived } from "svelte/store";
export var GameState;
(function (GameState) {
    GameState[GameState["Preparing"] = 0] = "Preparing";
    GameState[GameState["Ready"] = 1] = "Ready";
    GameState[GameState["Dealing"] = 2] = "Dealing";
    GameState[GameState["Selection"] = 3] = "Selection";
    GameState[GameState["Discard"] = 4] = "Discard";
    GameState[GameState["Drawing"] = 5] = "Drawing";
    GameState[GameState["Summary"] = 6] = "Summary";
    GameState[GameState["Clearing"] = 7] = "Clearing"; // animating summary/cards being cleared off screen
})(GameState || (GameState = {}));
export const gameState = writable(GameState.Preparing);
export const playerOneHand = writable([]);
export const playerOneScore = writable(null);
export const gameStateAndHand = derived([gameState, playerOneHand], stores => stores);
